<template>
    <div class="whole">

        <div v-if="showLogout" :class="menu_div">
            <NavMenu :nav-menus="menuData" :collapse="isCollapse" :active-index="activeIndex"
                     :openeds="openeds"></NavMenu>
        </div>
        <div>
            <div v-if="showLogout" class="logodiv">
                <div class="logo">
                    <div style="padding: 0 15px;" :class="show_menu" @click="toggleClick">
                        <i :class="menu_icon" style="font-size: 25px;padding-right: 15px;"></i>
                        {{address}}
                    </div>
                    <div class="logoutDiv">
                        <el-avatar :src="avatarUrl"></el-avatar>
                        <el-dropdown @command="sumTypeCkeck" class="drop_menu">
                          <span class="el-dropdown-link">
                            {{currUserName}}<i class="el-icon-arrow-down el-icon--right"></i>
                          </span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item command="owner">个人中心</el-dropdown-item>
                                <el-dropdown-item command="updatepwd">修改密码</el-dropdown-item>
                                <el-dropdown-item command="logout">注销</el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </div>
                </div>
            </div>
            <router-view :class="main_content"></router-view>
        </div>

    </div>
</template>

<script>
    import NavMenu from "./components/NavMenu.vue";

    export default {
        components: {
            NavMenu: NavMenu
        },
        data() {
            return {
                address: '',
                avatarUrl: '',
                menu_div: 'menu_div',
                main_content: 'content',
                show_menu: 'show_menu',
                menu_icon: 'el-icon-s-fold',
                isCollapse: false,
                activeIndex: '',
                menuData: [],
                openeds: [],
                showLogout: false,
                currUserName: '',

            }
        },
        watch: {
            '$route'(form) {
                this.showLogout = false;
                this.address = form.name;
                // console.log(this.$route)
                // console.log(this.$route.path!= '/loginManage/index')
                if (this.$route.path != '/loginManage/index') {
                    this.showLogout = true;
                }
                var href = window.location.href;
                if (href.indexOf("/index") == -1) {
                    href = href.slice(0, href.lastIndexOf('/')) + '/index';
                }
                if (this.$route.query.source == 'login') {
                    this.axios.get('/api/menu/get').then((response) => {
                        this.menuData = response.data.data;

                        this.menuData.forEach((item) => {
                            if (item.child) {
                                this.openeds.push(item.id)
                            }
                        });

                        if (this.menuData[0].child) {
                            this.$router.addRoutes([{path: '/', redirect: this.menuData[0].child[0].name}]);
                        } else {
                            this.$router.addRoutes([{path: '/', redirect: this.menuData[0].name}]);
                        }

                        let start = href.lastIndexOf('#') + 1;
                        let path = href.slice(start + 1);
                        let end = path.indexOf("?");
                        if (end == -1) {
                            this.activeIndex = response.data.data[0].name;
                        } else {
                            this.activeIndex = href.slice(start, start + 1 + end);
                        }
                    }).catch((response) => {
                        console.log(response);
                    })
                } else {
                    let start = href.lastIndexOf('#') + 1;
                    let path = href.slice(start + 1);
                    let end = path.indexOf("?");
                    if (end == -1) {
                        this.activeIndex = href.slice(start);
                    } else {
                        this.activeIndex = href.slice(start + 1, start + 1 + end);
                    }
                }
                this.axios.get('/open/user/get').then((response) => {
                    if (response.data.data != null && response.data.data.userId != null) {
                        this.currUserName = response.data.data.userName;
                        this.avatarUrl = response.data.data.avatarUrl;
                    }
                }).catch((response) => {
                    console.log(response);
                })
                this.axios.get('/open/system/init').then((response) => {
                        if (response.data.state == '0') {
                            let data = response.data.data;
                            // console.log(data)
                            if (data.system_title) {
                                document.title = data.system_title;
                                this.$cookies.set("systemprofiles_title", data.system_title, 3600 * 24 * 365);
                            }
                        }
                    }
                ).catch((response) => {
                    console.log(response);
                })
            }
        },
        methods: {
            sumTypeCkeck(type) {  // 下拉列表的点击事件
                if (type == 'logout') {
                    this.logout();
                }
                if (type == 'owner') {
                    this.$router.push({path: '/userManageOwner/index'});
                }
                if (type == 'updatepwd') {
                    this.$router.push({path: '/userManagePwd/index'});
                }
            }
            ,
            toggleClick() {
                if (this.isCollapse) {
                    this.menu_icon = 'el-icon-s-fold';
                    this.main_content = 'content';
                    this.show_menu = 'show_menu';
                    this.menu_div = 'menu_div';
                } else {
                    this.menu_icon = 'el-icon-s-unfold';
                    this.main_content = 'content1';
                    this.show_menu = 'hidden_menu';
                    this.menu_div = 'menu_div1';
                }
                this.isCollapse = !this.isCollapse;
            }
            ,
            logout() {
                this.$confirm('您确定要注销吗?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'info'
                }).then(() => {
                    this.axios.get('/api/logout').then(() => {
                        window.location.reload();
                        // this.$router.push({path: '/loginManage/index', replace: true});
                    })
                }).catch(() => {

                });
            }
            ,
        }
        ,
        beforeCreate() {
            this.axios.get('/api/menu/get').then((response) => {
                this.menuData = response.data.data;

                this.menuData.forEach((item) => {
                    if (item.child) {
                        this.openeds.push(item.id)
                    }
                });

                if (this.menuData[0].child) {
                    this.$router.addRoutes([{path: '/', redirect: this.menuData[0].child[0].name}]);
                } else {
                    this.$router.addRoutes([{path: '/', redirect: this.menuData[0].name}]);
                }

                let start = window.location.href.lastIndexOf('#') + 1;
                // let path = window.location.href.slice(start + 1);
                let path = window.location.href.slice(start);
                let end = path.indexOf("?");
                console.log('path  = ', path);
                if (end == -1) {
                    this.activeIndex = response.data.data[0].name;
                } else {
                    var href = window.location.href;
                    console.log('path href = ', href);
                    let start = window.location.href.lastIndexOf('#') + 1;

                    if (href.indexOf("/index") == -1) {
                        href = href.slice(start, href.lastIndexOf('/')) + '/index';
                        this.activeIndex = href.slice(start + 1, start + 1 + end);
                    } else {
                        href = href.slice(start, href.lastIndexOf('/')) + '/index';
                        this.activeIndex = href;
                    }
                }
            }).catch((response) => {
                console.log(response);
            })
        }
        ,

    }
</script>
<style scoped lang="less">
    .drop_menu {
        color: #606266;
        padding-left: 15px;
    }

    .menu_div {
        width: 250px;
        position: absolute;
        top: 0px;
        background: #304156;
        left: 0px;
        bottom: 0px;
    }

    .menu_div1 {
        width: 64px;
        position: absolute;
        top: 0px;
        background: #304156;
        left: 0px;
        bottom: 0px;
    }

    .el-menu {
        border-right: none;
    }

    .logodiv {
        height: 60px;
        width: 100%;
        background-color: #fff;
        margin-right: 0px;
        -webkit-box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
        box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
    }

    .logoutDiv {
        position: absolute;
        right: 20px;
        display: flex;
        align-items: center;
        /* font-size: 14px;*/
    }

    .logo {
        float: left;
        height: 56px;
        line-height: 56px;
        padding-left: 250px;
        padding-top: 2px;
        color: #606266;
        /*   font-weight: bold;
          font-size: 25px;
          background-repeat: no-repeat;*/
    }

    .content {
        position: absolute;
        top: 75px;
        left: 250px;
        right: 0;
        bottom: 0;
    }

    .content1 {
        position: absolute;
        top: 75px;
        left: 60px;
        right: 0;
        bottom: 0;
    }

    .show_menu {
        position: absolute;
        left: 250px;
    }

    .hidden_menu {
        position: absolute;
        left: 60px;
    }
</style>

<style>
    .el-header {
        height: 35px !important;
    }
</style>
