<template>
    <el-menu
            :default-active="activeIndex"
            :background-color="variables.menuBg"
            :text-color="variables.menuText"
            :unique-opened="false"
            :collapse-transition="false"

            :collapse="collapse"
            router
            :active-text-color="variables.menuActiveText">
        <template v-for="navMenu in navMenus">
            <el-menu-item :index="navMenu.name" :key="navMenu.name" v-if="!navMenu.child">
                <i :class="navMenu.icon"></i>
                <span slot="title">{{navMenu.alias}}</span>
            </el-menu-item>

            <el-submenu :index="navMenu.name" :key="navMenu.name" v-if="navMenu.child">
                <template slot="title">
                    <i :class="navMenu.icon"></i>
                    <span slot="title">{{navMenu.alias}}</span>
                </template>
                <template v-for="child in navMenu.child">
                    <el-menu-item :index="child.name" :key="child.name">{{child.alias}}</el-menu-item>
                </template>
            </el-submenu>

        </template>
    </el-menu>
</template>

<script>
    import variables from '@/styles/variables.scss'

    export default {
        props: ['navMenus', 'activeIndex', 'collapse', 'openeds'],
        computed: {
            variables() {
                return variables
            },
        },
        methods: {}
    }
</script>

<style scoped lang="less">
    .el-submenu .el-menu-item {
        min-width: 240px !important;
        background-color: #1f2d3d !important;
    }
</style>
